<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者: {{blog.author}}</p>
		<p>分类:</p>
		<ul>
			<li v-for="category in blog.categories" :key="category">
				{{category}}
			</li>
		</ul>
    </div>
</template>
<script>
export default {
    data() {
        return {
           id:this.$route.params.id,
           blog:{} 
        }
    },
    created () {
        this.$http.get("https://vue-demo-d2512.firebaseio.com/post/" + this.id + ".json")
        .then((res)=>{
            console.log(res);
            return res.json();
            // this.blog=res.body;
        })
        .then((res)=>{
            this.blog=res;
        })
    }
}
</script>
<style scoped>
#single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa;
}
</style>


