<template>
    <div id="show-blogs" v-theme:column="'narrow'">
         <h1>博客总览</h1>
         <input type="text" v-model="search" placeholder="搜索">
         <div v-for="(blog,index) in fltersearch" class="single-blog" :key="index">
         	<router-link v-bind:to="'/blog/' + blog.id">
            <h2 v-rainow>{{blog.title | to-uppercase}}</h2>
            </router-link>
             <article>{{blog.content | snippet}}</article>
         </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
           blogs:[],
           search:""
        }
    },
    created() {
        this.$http.get("https://vue-demo-d2512.firebaseio.com/post.json")
        .then((res)=>{
            return res.json();
            //  console.log(res.json());
            //    this.blogs=res.body.slice(0,10);
            // console.log(this.blogs);
        })
        .then((res)=>{
            var blogArray=[];
            for(let key in res){
                // console.log(res);
                // console.log(res[key]);
                res[key].id=key;
                blogArray.push(res[key]);
                // console.log(blogArray);
                this.blogs=blogArray;
                console.log(this.blogs);
            }
        })
    },
    computed: {
        fltersearch(){
           return this.blogs.filter((blog)=>{
                return blog.title.match(this.search);
           })
        }
    },
    //局部使用filters
    filters:{
        // 两种写法
        // "to-uppercase":function(value){
        //     return value.toUpperCase();
        // },
        toUppercase(value){
            return value.toUpperCase();
        }
    },
    //局部自定义指令使用
    directives:{
        'rainow':{
            bind(el,binding,vnode){
                el.style.color = "#" + Math.random().toString(16).slice(2,8);
            }
        }
    }
}
</script>
<style scoped>
#show-blogs{
	max-width: 800px;
	margin: 0 auto;
}
.single-blog{
	padding: 20px;
	margin: 20px 0;
	box-sizing: border-box;
	background: #eee;
    border: 1px dotted #aaa;
}
#show-blogs a{
  color: #444;
  text-decoration: none;
}

input[type="text"]{
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>


